import React, { useState, useEffect } from 'react';
import {
  Panel, PanelHeader, Div, Title, Button,
  FormLayout, FormLayoutGroup, Radio
} from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import questions from '../data/questions';
import sideImage from '../assets/555.png';
import './QuestionPanel.css';

const labels = ['–°–æ–≤—Å–µ–º –Ω–µ –±–µ—Å–ø–æ–∫–æ–∏—Ç','–°–ª–∞–±–æ','–£–º–µ—Ä–µ–Ω–Ω–æ','–°–∏–ª—å–Ω–æ','–û—á–µ–Ω—å —Å–∏–ª—å–Ω–æ'];

export default function QuestionPanel({ id, data, go }) {
  const { current, answers } = data;
  const [value, setValue] = useState(
    answers[current] != null ? String(answers[current]) : null
  );
  const [hoveredIndex, setHoveredIndex] = useState(null);
  const [answerWidth, setAnswerWidth] = useState(null);

  /* üÜï 1. –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º —Ä–µ–∫–ª–∞–º—É (–¥–µ–ª–∞–π—Ç–µ –≤ App.js –æ–¥–∏–Ω —Ä–∞–∑, –Ω–æ –∑–¥–µ—Å—å —Ç–æ–∂–µ —Å—Ä–∞–±–æ—Ç–∞–µ—Ç) */
  useEffect(() => {
    bridge.send('VKWebAppInitAds')
      .catch(err => console.log('InitAds error:', err));
  }, []);

  /* 2. –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –±–∞–Ω–Ω–µ—Ä –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –ø–∞–Ω–µ–ª–∏ */
  useEffect(() => {
    const show = async () => {
      try {
        // optional: –∑–∞–ø—Ä–æ—Å–∏—Ç—å –Ω–∞–ª–∏—á–∏–µ –±–∞–Ω–Ω–µ—Ä–∞, —á—Ç–æ–±—ã –Ω–µ –ª–æ–≤–∏—Ç—å Ads_No_Fill
        await bridge.send('VKWebAppShowBannerAd', {
          banner_location: 'bottom'
        });
        console.log('–ó–∞–ø—Ä–æ—Å –±–∞–Ω–Ω–µ—Ä–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω');
      } catch (error) {
        console.log('–û—à–∏–±–∫–∞ –ø–æ–∫–∞–∑–∞ –±–∞–Ω–Ω–µ—Ä–∞:', error);
      }
    };
    show();
  }, []);

  /* üÜï 3. –ø–æ–¥–ø–∏—Å—ã–≤–∞–µ–º—Å—è –Ω–∞ —Å–æ–±—ã—Ç–∏—è ‚Äï –∑–¥–µ—Å—å —Ñ–∏–∫—Å–∏—Ä—É–µ—Ç—Å—è —Ñ–∞–∫—Ç –ø–æ–∫–∞–∑–∞ */
  useEffect(() => {
    const handler = ({ detail }) => {
      switch (detail.type) {
        case 'VKWebAppBannerAdUpdated':
          console.log('‚úÖ –ë–∞–Ω–Ω–µ—Ä —Ä–µ–∞–ª—å–Ω–æ –ø–æ–∫–∞–∑–∞–Ω', detail.data); // –≤–æ—Ç —ç—Ç–æ—Ç –º–æ–º–µ–Ω—Ç –∏–¥—ë—Ç –≤ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É
          break;
        case 'VKWebAppBannerAdClosedByUser':
          console.log('‚ùå –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞–∫—Ä—ã–ª –±–∞–Ω–Ω–µ—Ä');
          break;
        default:
          break;
      }
    };
    bridge.subscribe(handler);
    return () => bridge.unsubscribe(handler);
  }, []);

  /* ‚Ä¶ –æ—Å—Ç–∞–ª—å–Ω–æ–π –≤–∞—à –∫–æ–¥ –±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π ‚Ä¶ */

  /* –°–±—Ä–æ—Å –æ—Ç–≤–µ—Ç–∞ –ø—Ä–∏ —Å–º–µ–Ω–µ –≤–æ–ø—Ä–æ—Å–∞ */
  useEffect(() => {
    setValue(answers[current] != null ? String(answers[current]) : null);
  }, [current, answers]);

  /* –í—ã—á–∏—Å–ª—è–µ–º —à–∏—Ä–∏–Ω—É —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  useEffect(() => {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    ctx.font = window.getComputedStyle(document.body).font;
    const widths = labels.map(l => ctx.measureText(l).width);
    setAnswerWidth(Math.max(...widths) + 8 + 12 + 40);
  }, []);

  const next = () => {
    if (value == null) return;
    go({ type: 'answer', payload: { index: current, answer: Number(value) } });
  };

  const headerText = `–í–æ–ø—Ä–æ—Å ${current + 1} / ${questions.length}`;

  return (
    <Panel id={id} className="qp-panel-wrapper" style={{ position: 'relative', backgroundColor: 'transparent' }}>
      {/* –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å–ø—Ä–∞–≤–∞ */}
      <img src={sideImage} alt="" style={{ position:'absolute',top:111,left:250,width:500,zIndex:1 }} />

      <PanelHeader style={{ backgroundColor:'transparent' }} separator={false}>
        <div className="qp-header-label">{headerText}</div>
      </PanelHeader>

      <Div style={{ padding:16,backgroundColor:'transparent' }}>
        <Title level="3" weight="bold" className="qp-question-label" style={{ whiteSpace:'normal',overflowWrap:'break-word' }}>
          {questions[current]}
        </Title>

        <FormLayout>
          <FormLayoutGroup mode="vertical">
            {labels.map((label, index) => (
              <div
                key={index}
                className={`qp-answer-wrapper ${hoveredIndex===index?'hovered':''}`}
                onMouseEnter={() => setHoveredIndex(index)}
                onMouseLeave={() => setHoveredIndex(null)}
                style={{ width: answerWidth ? `${answerWidth}px` : 'auto' }}
              >
                <Radio
                  name={`q_${current}`}
                  value={String(index)}
                  checked={value === String(index)}
                  onChange={() => setValue(String(index))}
                  className="qp-answer-radio"
                >
                  {label}
                </Radio>
              </div>
            ))}
          </FormLayoutGroup>
        </FormLayout>

        <Div style={{ textAlign:'center',marginTop:20 }}>
          <Button size="l" disabled={value == null} onClick={next}>
            {current === questions.length - 1 ? '–ó–∞–≤–µ—Ä—à–∏—Ç—å' : '–î–∞–ª–µ–µ'}
          </Button>
        </Div>
      </Div>
    </Panel>
  );
}