/* 1. –°—Ç–∏–ª–∏ VK UI (–¥–æ–ª–∂–Ω—ã –ø–æ–¥–∫–ª—é—á–∞—Ç—å—Å—è –î–û –≤–∞—à–µ–≥–æ CSS) */
import '@vkontakte/vkui/dist/vkui.css';

