/* 7. (–ù–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ) –º–∞–ª–µ–Ω—å–∫–∏–π —Ö–∞–∫, —á—Ç–æ–±—ã –≤ dev-—Ä–µ–∂–∏–º–µ
      —Ä–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∞—Ç—å –∫–ª–∏–∫–∏, –µ—Å–ª–∏ –≤–¥—Ä—É–≥ VKUI –Ω–∞–ª–æ–∂–∏—Ç overlay */
