import React, { useReducer, useEffect, useState } from 'react';
import {
  AppRoot,
  SplitLayout,
  SplitCol,
  View,
  ModalRoot,
  ModalCard,
  Button
} from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';

import HomePanel from './components/HomePanel';
import QuestionPanel from './components/QuestionPanel';
import ResultPanel from './components/ResultPanel';

const initialState = {
  started: false,
  current: 0,
  answers: Array(20).fill(null),
};

function reducer(state, action) {
  switch (action.type) {
    case 'start':
      return { ...initialState, started: true };
    case 'answer': {
      const { index, answer } = action.payload;
      const answers = [...state.answers];
      answers[index] = answer;
      return { ...state, current: index + 1, answers };
    }
    case 'restart':
      return { started: true, current: 0, answers: Array(20).fill(null) };
    default:
      return state;
  }
}

const slides = [
  {
    title: '–ß—Ç–æ —ç—Ç–æ?',
    text: '–≠—Ç–æ –º–∏–Ω–∏-–ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –¥–ª—è —Å–∞–º–æ—Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –Ω–∞ –ø–æ—Å—Ç—Ç—Ä–∞–≤–º–∞—Ç–∏—á–µ—Å–∫–æ–µ —Å—Ç—Ä–µ—Å—Å–æ–≤–æ–µ —Ä–∞—Å—Å—Ç—Ä–æ–π—Å—Ç–≤–æ. '
  },
  {
    title: '–û —Ç–µ—Å—Ç–µ',
    text: '–í–æ–ø—Ä–æ—Å—ã —Ç–µ—Å—Ç–∞ –æ—Å–Ω–æ–≤–∞–Ω—ã –Ω–∞ –∫–æ–Ω—Ç—Ä–æ–ª—å–Ω–æ–º —Å–ø–∏—Å–∫–µ –ü–¢–°–† –¥–ª—è DSM-5, –∫–æ—Ç–æ—Ä—ã–π –æ—Ü–µ–Ω–∏–≤–∞–µ—Ç 20 —Å–∏–º–ø—Ç–æ–º–æ–≤ –¥–∞–Ω–Ω–æ–≥–æ —Ä–∞—Å—Å—Ç—Ä–æ–π—Å—Ç–≤–∞.'
  },
  {
    title: '–†–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏',
    text: '–ü–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –º–æ–∂–µ—Ç –ø—Ä–µ–¥–ª–æ–∂–∏—Ç—å –≤–∞–º –ø—Ä–æ—Å—Ç—ã–µ —Ç–µ—Ö–Ω–∏–∫–∏ –¥–ª—è —Å–∞–º–æ–ø–æ–º–æ—â–∏ –∏ —Å–æ–±–ª—é–¥–µ–Ω–∏—è –ø—Å–∏—Ö–æ–≥–∏–≥–∏–µ–Ω—ã.'
  },
  {
    title: '–í–∞–∂–Ω–æ –∑–Ω–∞—Ç—å',
    text: '–†–µ–∑—É–ª—å—Ç–∞—Ç –Ω–µ –∑–∞–º–µ–Ω—è–µ—Ç –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—é –≤—Ä–∞—á–∞. –û–Ω –º–æ–∂–µ—Ç –ø–æ–¥—Å–∫–∞–∑–∞—Ç—å, —Å—Ç–æ–∏—Ç –ª–∏ –æ–±—Ä–∞—Ç–∏—Ç—å—Å—è –∑–∞ –ø–æ–º–æ—â—å—é.'
  },
  
  {
    title: '–ü–æ–º–Ω–∏—Ç–µ:',
    text: '–ó–∞–±–æ—Ç–∞ –æ –º–µ–Ω—Ç–∞–ª—å–Ω–æ–º –∑–¥–æ—Ä–æ–≤—å–µ —Ç–∞–∫ –∂–µ –≤–∞–∂–Ω–∞, –∫–∞–∫ –∏ –æ –∑–¥–æ—Ä–æ–≤—å–µ —Ç–µ–ª–∞ ‚ù§Ô∏è'
  }
];

export default function App() {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [userInfo, setUserInfo] = useState(null);
  const [modal, setModal] = useState(null);
  const [slideIndex, setSlideIndex] = useState(0);

  useEffect(() => {
    (async () => {
      try {
        await bridge.send('VKWebAppInit');
        const user = await bridge.send('VKWebAppGetUserInfo');
        setUserInfo(user);
      } catch (e) {
        console.log('VK Bridge error:', e);
      }
    })();

    const lastShown = localStorage.getItem('slidesLastShown');
    const now = Date.now();
    if (!lastShown || now - parseInt(lastShown) > 172800000) {
      setModal('intro');
      localStorage.setItem('slidesLastShown', now.toString());
    }
  }, []);

  const nextSlide = () => setSlideIndex((prev) => (prev + 1) % slides.length);
  const prevSlide = () => setSlideIndex((prev) => (prev - 1 + slides.length) % slides.length);

  const activePanel =
    !state.started ? 'home' :
    state.current < 20 ? 'question' :
    'result';

  const modalRoot = (
    <ModalRoot activeModal={modal} onClose={() => setModal(null)}>
      <ModalCard
        id="intro"
        onClose={() => setModal(null)}
        actions={
          <Button size="l" mode="primary" onClick={() => setModal(null)}>
            –ü–æ–Ω—è—Ç–Ω–æ
          </Button>
        }
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 'auto',
          maxWidth: '100vw',
          minHeight: '100vh',
          boxSizing: 'border-box',
        }}
      >
        <div
          style={{
            border: '0px solid #5ea4ff',
            borderRadius: 16,
            backgroundColor: '#fff',
            padding: 20,
            boxSizing: 'border-box',
            width: '100%',
            height: 280,
            position: 'relative',
            overflow: 'hidden',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column'
          }}
        >
          {slides.map((slide, i) => (
            <div
              key={i}
              style={{
                position: 'absolute',
                top: -30,
                left: 0,
                right: 0,
                bottom: 0,
                margin: 'auto',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                textAlign: 'center',
                padding: '20 20px',
                width: '100%',
                opacity: i === slideIndex ? 1 : 0,
                pointerEvents: i === slideIndex ? 'auto' : 'none',
                zIndex: i === slideIndex ? 2 : 1,
                transition: 'opacity 0.4s ease-in-out'
              }}
            >
              <h3>{slide.title}</h3>
              <p>{slide.text}</p>
            </div>
          ))}
          <div style={{ position: 'relative', marginTop: 200, display: 'flex', flexDirection: 'column', alignItems: 'center', zIndex: 3 }}>
            <div style={{ display: 'flex', gap: 10 }}>
              <Button mode="secondary" size="s" onClick={prevSlide}>‚Üê</Button>
              <Button mode="secondary" size="s" onClick={nextSlide}>‚Üí</Button>
            </div>
            <div style={{ marginTop: 10, display: 'flex', gap: 8 }}>
              {slides.map((_, i) => (
                <div
                  key={i}
                  style={{
                    width: 8,
                    height: 8,
                    borderRadius: '50%',
                    backgroundColor: i === slideIndex ? '#5ea4ff' : '#ccc'
                  }}
                />
              ))}
            </div>
          </div>
        </div>
      </ModalCard>
    </ModalRoot>
  );

  return (
    <AppRoot>
      <SplitLayout modal={modalRoot}>
        <SplitCol>
          <View activePanel={activePanel} key={activePanel}>
            <HomePanel id="home" userInfo={userInfo} go={dispatch} />
            <QuestionPanel id="question" data={state} go={dispatch} />
            <ResultPanel id="result" data={state} go={dispatch} />
          </View>
        </SplitCol>
      </SplitLayout>
    </AppRoot>
  );
}
